@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
body {
  font-family: "Hind", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 22px;
  color: #777777;
}

main {
  overflow: hidden;
}

@media screen and (min-width: 1025px) {
  .container, main .wp-block-group .wp-block-group__inner-container {
    max-width: 990px;
  }
}
@media screen and (min-width: 1230px) {
  .container, main .wp-block-group .wp-block-group__inner-container {
    max-width: 1200px;
  }
}
@media screen and (max-width: 1024px) {
  .container, main .wp-block-group .wp-block-group__inner-container {
    max-width: 90%;
  }
}
@media screen and (max-width: 749px) {
  .container, main .wp-block-group .wp-block-group__inner-container {
    max-width: 100%;
  }
}
